/* Mobile */
@media screen and (max-width: 768px) {
    .text-diagram {
        font-family: 'Heebo', sans-serif;
        font-size: 4.7vw;
        color: grey;
        text-align: center;
        width: 86vw;
        padding: 5vw;
        position: relative;
        margin-top: 4vh;
        min-height: 0;
        direction: rtl;
    }

    .marselLogo {
        width: 25vw;
        height: auto;
        position: absolute;
        top: 35vh;
        right: 15vw;
        rotate: 30deg;
    }

    .diagram-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3vh;
        /* Adjusted spacing */
        margin-top: 20px;
    }

    .diagram-item-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2vh;
        /* Space between the item and the arrow */
    }

    .diagram-item {
        width: 150px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px dashed #f2903f;
        background: none;
        padding: 10px;
        text-align: center;
        border-radius: 2vh;
        font-family: 'Karantina';
        color: #f2903f;
        font-size: 8vmin;
    }

    .up-arrow {
        width: 30px;
        height: auto;
    }

    .readDiv {
        background-color: #f2903f;
        border: 2px solid #f2903f;
        color: white;
    }
}


/* Desktop */
@media screen and (min-width: 1025px) {
    .text-diagram {
        font-family: 'Heebo', sans-serif;
        font-size: 1.2vw;
        color: grey;
        text-align: center;
        width: 50vw;
        padding: 2vw;
        direction: rtl;
    }

    .diagram-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3vh;
        margin-top: -20px;
    }

    .diagram-item-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2vh;
        height: 9vh;
    }

    .diagram-item {
        width: 201px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px dashed #f2903f;
        background: none;
        padding: 10px;
        text-align: center;
        border-radius: 2vh;
        font-family: 'Karantina';
        color: #f2903f;
        font-size: 5vmin;
        cursor: pointer;
    }

    .up-arrow {
        width: 30px;
        height: auto;
        margin-top: -1vh;
    }

    .readDiv {
        background-color: #f2903f;
        border: 2px solid #f2903f;
        color: white;
    }

    /* הסרת החץ האחרון */
    .diagram-item-wrapper:nth-child(4)::after {
        display: none;
    }

    .readDiv {
        background-color: #f2903f;
        border: 2px solid #f2903f;
        color: white;
    }

    .marselLogo {
        width: 5vw;
        height: auto;
        position: absolute;
              top: 21vh;
        right: 25vw;
        rotate: 30deg;
        z-index: 1;
    }

}